import {
  Button,
  ChakraProvider,
  ColorModeProvider,
  Flex,
  Text,
  Textarea,
  useColorMode,
  useColorModeValue,
} from "@chakra-ui/react";
import { BsFillCloudSunFill } from "react-icons/bs";
import { useState } from "react";
import DarkMode from "./DarkMode";

function App() {
  const [text, setText] = useState("");
  const [error, setError] = useState("");
  const bg = useColorModeValue("white", "gray.800");
  const fontColor = useColorModeValue("black", "white");

  const handleInputChange = (e) => {
    let inputValue = e.target.value;
    setText(inputValue);
  };
  const formatText = () => {
    try {
      let newText = "";
      for (let i = 0; i < text.length; i++) {
        if (text[i] == "[") {
          while (i < text.length) {
            if (text[i] == "]") {
              i++;
              break;
            }
            i++;
          }
        }
        newText += text[i];
      }
      setText(removeSpacesBeforeCommas(newText));
    } catch (e) {
      setError(e);
    }
  };
  const removeTextInParanthesis = () => {
    try {
      let newText = "";
      for (let i = 0; i < text.length; i++) {
        if (text[i] == "(") {
          while (i < text.length) {
            if (text[i] == ")") {
              i++;
              break;
            }
            i++;
          }
        }
        newText += text[i];
      }
      setText(removeSpacesBeforeCommas(newText));
    } catch (e) {
      setError(e);
    }
  };
  const removeSpacesBeforeCommas = (str) => {
    let newText = "";
    for (let i = 0; i < str.length; i++) {
      if (str[i] == " " && (str[i + 1] == " " || str[i + 1] == ",")) {
        continue;
      }
      newText += str[i];
    }
    return newText;
  };
  return (
    <Flex
      className="App"
      w="100vw"
      h="100vh"
      justifyContent="center"
      alignItems="center"
      bg="gray.200"
      overflow="hidden"
    >
      <Flex
        w="50%"
        flexDir="column"
        gap="40px"
        boxShadow={"dark-lg"}
        px="20"
        py="10"
        borderRadius={"3xl"}
        bg={bg}
        alignItems={"center"}
        textColor={fontColor}
      >
        <Text fontFamily={""} textAlign={"center"} fontSize="5xl">
          Wikipedia Text Formatter
        </Text>
        <DarkMode />
        <Text>{error}</Text>
        <Textarea
          h="200px"
          placeholder="Paste your text here..."
          value={text}
          onChange={handleInputChange}
        ></Textarea>
        <Flex justifyContent={"space-around"} w="100%">
          <Button onClick={formatText} colorScheme={"blue"}>
            Format
          </Button>
          <Button colorScheme="purple" onClick={removeTextInParanthesis}>
            Remove Text in Paranthesis
          </Button>
          <Button colorScheme={"green"}>Copy to Clipboard</Button>
        </Flex>
      </Flex>
    </Flex>
  );
}

export default App;
